  ul.baraja-container {
    width: 11vw;
    height: 16vw;
    min-width: 70px;
    min-height: 100px;
    margin: 0 auto 30px;
    position: relative;
    padding: 0;
    list-style-type: none;
  }
  
  ul.baraja-container li {
    width: 100%;
    height: 100%;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
    background: #fff;
    pointer-events: auto;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  